.contacts{
    width: 100%;
    height: auto;
    padding: 1rem;

    &__box1{
        width: 100%;
        height: auto;

        &__block{
            display: flex;
            justify-content: space-between;
            align-items: center;
            grid-gap: 1rem;
            margin-top: 1rem;
            svg{
                width: 40px;
                height: 40px;
            }
            &__input{
                width: 100%;
                padding: 1rem;
                border-radius: 8px;
                border: 1px solid #ccc
            }

            &__edit{
                padding: 1rem;
                border-radius: 8px;
                border: none;
                cursor: pointer;
                background-color: #125082;
                color: #fff;
                font-size: 1rem;
                border: none;
                width: 20%;
            }

            button{
                padding: 1rem;
                border-radius: 8px;
                border: none;
                cursor: pointer;
                background-color: #44963B;
                color: #fff;
                font-size: 1rem;
                border: none;
                width: 20%;
            }
        }
    }


}